<template>
  <v-card style="margin: 1%">
    <v-list-item two-line>
      <v-list-item-content>
        <v-list-item-title class="text-h5">
          {{ stock_name }}
        </v-list-item-title>
        <v-list-item-subtitle>取值时刻：{{$store.state.selected}}</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>

    <v-divider></v-divider>

    <v-card-text class="body-1 text-center text--primary">
      <v-row>
        <v-col cols="12" lg="4" v-for="(factor, index) in factors" :key="index">
          {{ Object.keys(factor)[0] + "：" + factor[Object.keys(factor)[0]] }}
        </v-col>
      </v-row>
    </v-card-text>

    <v-divider></v-divider>
    <info-timeline ref="timeline" ></info-timeline>
  </v-card>
</template>
<script>
import InfoTimeline from "@/components/InfoTimeline";
import {cleanFactor} from "@/utils";

export default {
  components: { InfoTimeline },
  data() {
    return {
      stock_id: "",
      stock: {},
      stock_name: "股票名称",
      factors: [],
    };
  },
  methods: {
    getFactorRank(allStocks, factor) {
      allStocks = allStocks.sort((a, b) => (b[factor] - a[factor]));
      return allStocks.findIndex((value) => value.stock_id === this.stock_id) + 1;
    },
    initInfo() {
      this.stock_id = this.$store.state.item.stock_id;
      this.stock = this.$store.state.item;
      this.stock_name = this.$store.state.item._stock_name;

      let metaData = JSON.parse(JSON.stringify(this.$store.state.metaData));
      let metaStock = JSON.parse(
        JSON.stringify(this.$store.state.metaData)
      ).filter((item) => item.stock_id === this.stock_id)[0];
      cleanFactor(metaStock);
      for (let key in metaStock) {
        let rank = this.getFactorRank(metaData, key);
        this.factors.push({
          [key]: metaStock[key] + "（" + rank + "|" + metaData.length + "）",
        });
      }
    },
  },
  created() {
    this.initInfo();
  },
};
</script>
