import { axios } from "./request";
import { URL } from "./base_url";

export function getHistoryTime(id) {
    return axios({
        url: `${URL}/historyTime/${id}`,
        method: "get",
    });
}

export function getBestTime(id,time) {
    return axios({
        url: `${URL}/most_similar/${id}/${time}`,
        method: "get",
    });
}

export function getInfo(type,id) {
    return axios({
        url: `${URL}/${type}/${id}`,
        method: "get",
    });
}
export function getCompareAvg(id,time) {
    return axios({
        url: `${URL}/compare/${id}/${time}`,
        method: "get",
    });
}

export function getKline(id,time) {
    return axios({
        url: `${URL}/klines/${id}/${time}`,
        method: "get",
    });
}
