<template>
  <v-row no-gutters>
    <v-col cols="12" lg="4">
      <v-card style="margin: 2%" elevation="0">
        <v-card-title class="justify-center">资讯</v-card-title>
        <v-divider></v-divider>
        <div style="max-height: 10px">
          <v-card
              elevation="0"
              :class="['overflow-y-auto', 'overflow-x-hidden']"
              min-height="200"
              max-height="400"
          >
            <div v-if="information!==null">

              <v-timeline align-top>
                <v-timeline-item
                  v-for="(item, index) in information"
                  :key="index"
                  :color="color_list[index % 2]"
                  small
                  right
                >
                  <template v-slot:opposite>
                    <span>{{ item.date }}</span>
                  </template>
                  <v-row class="pt-1">
                    <v-col>
                      <div>{{ item.info }}</div>
                    </v-col>
                  </v-row>
                </v-timeline-item>
              </v-timeline>
            </div>
            <div v-if="information===null" style="height: 100%;width: 100%;display: flex;align-items: center;justify-content: center">
              <div class="text-body-1" style="margin-top: 50px">暂无资讯</div>

            </div>
          </v-card>
        </div>
      </v-card>
    </v-col>
    <v-col cols="12" sm="12" lg="4">
      <v-card style="margin: 2%" elevation="0">
        <v-card-title class="justify-center">公告</v-card-title>
        <v-divider></v-divider>
        <v-card
            elevation="0"
            :class="['overflow-y-auto', 'overflow-x-hidden']"
            min-height="200"
            max-height="400"
        >
          <div v-if="notice!==null">
            <v-timeline align-top>
              <v-timeline-item
                v-for="(item, index) in notice"
                :key="index"
                :color="color_list[index % 2]"
                small
                right
              >
                <template v-slot:opposite>
                  <span>{{ item.date }}</span>
                </template>
                <v-row class="pt-1">
                  <v-col>
                    <div>{{ item.info }}</div>
                  </v-col>
                </v-row>
              </v-timeline-item>
            </v-timeline>
          </div>
          <div v-if="notice===null" style="height: 100%;width: 100%;display: flex;align-items: center;justify-content: center">
            <div class="text-body-1" style="margin-top: 50px">暂无公告</div>

          </div>
        </v-card>
      </v-card>
    </v-col>
    <v-col cols="12" sm="12" lg="4">
      <v-card style="margin: 2%" elevation="0">
        <v-card-title class="justify-center">新闻</v-card-title>
        <v-divider></v-divider>
        <v-card
            elevation="0"
            :class="['overflow-y-auto', 'overflow-x-hidden']"
            min-height="200"
            max-height="400"
        >

          <div v-if="news!==null">
            <v-timeline align-top>
              <v-timeline-item
                v-for="(item, index) in news"
                :key="index"
                :color="color_list[index % 2]"
                small
                right
              >
                <template v-slot:opposite>
                  <span>{{ item.date }}</span>
                </template>
                <v-row class="pt-1">
                  <v-col>
                    <div>{{ item.info }}</div>
                  </v-col>
                </v-row>
              </v-timeline-item>
            </v-timeline>
          </div>
          <div v-if="news===null" style="height: 100%;width: 100%;display: flex;align-items: center;justify-content: center">
            <div class="text-body-1" style="margin-top: 50px">暂无新闻</div>

          </div>
        </v-card>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import {getInfo} from "@/api/history_api";
import {notify} from "@/components/notification";

export default {
  name: "infoTimeline",
  data() {
    return {
      stock_id:"",
      news: null,
      notice: null,
      information: null,
      color_list: ["pink", "teal lighten-3"],
    };
  },
  methods:{
    initTimeLine(){
      console.log(this.$store.state.item)
      this.stock_id=this.$store.state.item.stock_id;
      let info_list = ["news", "notice", "information"];
      for (let info of info_list) {
        getInfo(info, this.stock_id)
            .then((res) => {
              if (res.data.status === 200) {
                if(res.data.data.length===0){
                  this.$set(this, info, null);
                }else{
                  this.$set(this, info, res.data.data);
                }
              } else {
                notify("error", res.data.err);
              }
            })
            .catch();
      }

    }
  },
  created() {
    this.initTimeLine();
  }
}
</script>

